@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);
*{border:0;margin:0;padding:0;font-family:'Share Tech Mono', monospace}.App{width:80%;margin:auto}.terminal-bar{background-color:#504644;margin-top:2em;border-radius:5px 5px 0 0;display:flex}.terminal-bar span{border-radius:50%;height:15px;margin:5px;width:15px}.terminal-bar #option1{background-color:#2196F3}.terminal-bar #option2{background-color:#FFBD2E}.terminal-bar #option3{background-color:#FF5F57}.terminal-cmds{background-color:#333333;overflow-y:scroll;padding:10px 0 200px 10px;color:#23ff00 !important;font-size:15px}.terminal-cmds .cmd-input{background:none;border:none;padding:0;color:#23ff00 !important;outline:none;margin-left:10px;font-size:15px}.terminal-cmds .cmd{letter-spacing:.5px}

